import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B6"
                meta="Toda la información que necesitas sobre la vitamina B6. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B6?"
                description="Toda la información que necesitas sobre la vitamina B6. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B6?"
                lang="es"
            />
            <h1>Los beneficios y las fuentes de alimentaci&oacute;n de la vitamina B6</h1>

            <PostIndex2 linksData={[
                {"url": "#benefits", "text": "Beneficios para la salud"},
                {"url": "#Necesidad", "text": "Necesidad diaria"},
                {"url": "#food", "text": "Fuentes de alimento"},
                {"url": "#deficiency", "text": "Deficiencia"},
                {"url": "#suplementos", "text": "Suplementos"},
            ]}
            />
            <hr />
            <p class="introductionText">La vitamina B6 es una de <a className="link" href="/vitaminaB">las vitaminas B</a> que beneficia al sistema nervioso central.
                Est&aacute; involucrada en la producci&oacute;n de los neurotransmisores serotonina y norepinefrina, y
                en la formaci&oacute;n de la mielina.</p>
            <hr />
            <p></p>
            <p>Tambi&eacute;n conocida como piridoxina, la vitamina B6 es una vitamina soluble en agua, lo que significa
                que se disuelve en agua. El cuerpo no la almacena y se excreta en la orina, por lo que la gente necesita
                ingerir vitamina B6 todos los d&iacute;as. Es parte de la familia de las vitaminas del complejo B.</p>
            <p></p>
            <p>Otras funciones de la piridoxina incluyen el metabolismo de las prote&iacute;nas y la glucosa, y la
                fabricaci&oacute;n de hemoglobina.</p>
            <p></p>
            <p>La hemoglobina es un componente de los gl&oacute;bulos rojos. Transporta el ox&iacute;geno. La vitamina
                B6 tambi&eacute;n est&aacute; involucrada en el mantenimiento de los n&oacute;dulos linf&aacute;ticos,
                el timo y el bazo saludables.</p>
            <p></p>
            <h2 name="benefits">Beneficios potenciales de la vitamina B6 para la salud</h2>
            <p>La vitamina B6 es vital para varias funciones corporales y se puede encontrar en muchos alimentos.</p>
            <p>La vitamina B6 tiene muchas funciones en el cuerpo, y juega un papel en m&aacute;s de 100 reacciones
                enzim&aacute;ticas.</p>
            <p></p>
            <h4>Funci&oacute;n cerebral</h4>
            <p></p>
            <p>La vitamina B6 puede ayudar a mejorar el rendimiento del cerebro. Un estudio publicado en el American
                Journal of Clinical Nutrition encontr&oacute; que las personas con altas concentraciones de vitamina B-6
                se evaluaron mejor en dos medidas de la funci&oacute;n de la memoria.</p>
            <p></p>
            <p>La vitamina B6 es necesaria para la transmisi&oacute;n de neuronas en el cerebro.</p>
            <p></p>
            <p>Investigadores de la Universidad de Oxford en Inglaterra observaron que tomar altas dosis de vitaminas B
                cada d&iacute;a era beneficioso para las personas mayores con un leve deterioro cognitivo. La tasa a la
                que sus cerebros se encogieron se redujo en un 50 por ciento, disminuyendo potencialmente su riesgo de
                demencia.</p>
            <p></p>
            <h4>N&aacute;useas durante el embarazo</h4>
            <p></p>
            <p>En una investigaci&oacute;n publicada en el American Journal of Obstetrics and Gynecology se
                lleg&oacute; a la conclusi&oacute;n de que la piridoxina puede reducir la gravedad de las n&aacute;useas
                en las primeras etapas del embarazo.</p>
            <p></p>
            <p>Sin embargo, se necesitan m&aacute;s estudios de alta calidad para confirmarlo. No use m&aacute;s de la
                dosis recomendada si est&aacute; tomando un suplemento.</p>
            <p></p>
            <h4>Protecci&oacute;n contra la contaminaci&oacute;n del aire</h4>
            <p></p>
            <p>Un estudio publicado en el PNAS en 2017 indic&oacute; que la vitamina B6 puede ayudar a proteger contra
                la contaminaci&oacute;n atmosf&eacute;rica, reduciendo el impacto de la contaminaci&oacute;n en el
                epigenoma.</p>
            <p></p>
            <p>Los investigadores esperan que sus hallazgos puedan conducir a nuevas medidas para prevenir los cambios
                epigen&eacute;ticos que pueden resultar de la exposici&oacute;n a la contaminaci&oacute;n del aire.</p>
            <p></p>
            <p>La vitamina B6 puede ayudar a proteger el cuerpo contra la contaminaci&oacute;n del aire.</p>
            <p>Se estima que el 92 por ciento de la poblaci&oacute;n mundial vive en lugares donde los l&iacute;mites de
                contaminaci&oacute;n son superiores al l&iacute;mite superior de 10 de la Organizaci&oacute;n Mundial de
                la Salud &mu;g/m3, lo que podr&iacute;a ser un hallazgo importante a la hora de controlar las
                enfermedades provocadas por el medio ambiente.</p>
            <p></p>
            <p>La vitamina B6 es importante por muchas razones. Una es asegurar el funcionamiento normal de las enzimas
                digestivas que descomponen los alimentos, mantienen la piel sana y producen productos sangu&iacute;neos
                como los gl&oacute;bulos rojos.</p>
            <p></p>
            <p>Esta importante vitamina tiene muchos otros usos, y una deficiencia puede conducir a un da&ntilde;o
                nervioso permanente.</p>
            <p></p>
            <h2 name="Necesidad">&iquest;Cu&aacute;nta vitamina B6 debo tomar cada d&iacute;a?</h2>
            <p>Hay una serie de factores que afectan a las necesidades diarias de vitamina B6 de una persona, ya que
                influyen en varios aspectos del metabolismo. El efecto sobre la ingesta de prote&iacute;nas ha sido
                ampliamente estudiado.</p>
            <p></p>
            <p>Las personas que siguen dietas altas en prote&iacute;nas pueden necesitar tomar m&aacute;s vitamina B6.
                Deber&iacute;an hablar con su m&eacute;dico.</p>
            <p></p>
            <p>Seg&uacute;n la Oficina de Suplementos Diet&eacute;ticos de los Estados Unidos, el requerimiento diario
                recomendado de vitamina B6 se muestra en la tabla siguiente. <b>Todos los valores estan representados en la unidad de mg.</b></p>
            <p></p>
            <b></b>
            <div class="dataTable">
                <tbody>
                <tr>
                    <td><b>Edad</b></td>
                    <td><b>Masculino</b></td>
                    <td><b>Hembra</b></td>
                </tr>
                <tr>
                    <td>0 a 6 meses</td>
                    <td>0.1 </td>
                    <td>0.1 </td>
                </tr>
                <tr>
                    <td> de 7 a 12 meses</td>
                    <td>0.3 </td>
                    <td>0.3 </td>
                </tr>
                <tr>
                    <td> de 1 a 3 años</td>
                    <td>0.5 </td>
                    <td>0.5 </td>
                </tr>
                <tr>
                    <td> de 4 a 8 años</td>
                    <td>0.6 </td>
                    <td>0.6 </td>
                </tr>
                <tr>
                    <td> de 9 a 13 años</td>
                    <td>1.0 </td>
                    <td>1.0 </td>
                </tr>
                <tr>
                    <td>14 a 18 años</td>
                    <td>1.3 </td>
                    <td>1.3 </td>
                </tr>
                <tr>
                    <td>19 a 50 años</td>
                    <td>1.3 </td>
                    <td>1.3 </td>
                </tr>
                <tr>
                    <td>51+ años</td>
                    <td>1.7 </td>
                    <td>1.5 </td>
                </tr>
                <tr>
                    <td>Durante el embarazo</td>
                    <td>-</td>
                    <td>1.9 </td>
                </tr>
                <tr>
                    <td>Durante la lactancia</td>
                    <td>-</td>
                    <td>2.0 </td>
                </tr>
                </tbody>
            </div>

            <h2 name="food">Fuentes alimenticias de vitamina B6</h2>
            <p>La mayor&iacute;a de los alimentos tienen algo de vitamina B6. Una persona con una dieta bien equilibrada
                no deber&iacute;a tener una deficiencia, a menos que tenga un problema f&iacute;sico o
                est&eacute; tomando ciertos medicamentos.</p>
            <p></p>
            <p>El at&uacute;n de aleta amarilla es s&oacute;lo una de las muchas fuentes de vitamina B6.</p>
            <p>Las siguientes son buenas fuentes de B6:</p>
            <p></p>
            <ul>
                <li>Garbanzos</li>
                <li>H&iacute;gado de ternera</li>
                <li>At&uacute;n de aleta amarilla</li>
                <li>Pechuga de pollo asado</li>
                <li>Un pl&aacute;tano mediano</li>
                <li>Tofu</li>
            </ul>
            <p></p>

            <p></p>
            <p></p>
            <p></p>
            <h2 name="deficiency">Deficiencia de B6</h2>
            <p>Las deficiencias son raras, pero pueden ocurrir si el individuo tiene una mala absorci&oacute;n
                intestinal o si est&aacute; tomando estr&oacute;genos, corticoesteroides, anticonvulsivos y algunos
                otros medicamentos.</p>
            <p></p>
            <p>El consumo excesivo de alcohol a largo plazo puede eventualmente resultar en una deficiencia de B6, al
                igual que el hipotiroidismo y la diabetes.</p>
            <p></p>
            <p>Los signos y s&iacute;ntomas de la deficiencia de vitamina B6 incluyen:</p>
            <p></p>
            <ul>
                <li>Neuropat&iacute;a perif&eacute;rica con hormigueo, entumecimiento y dolor en las manos y los pies.
                </li>
                <li>Anemia</li>
                <li>Incautaciones</li>
                <li>Depresi&oacute;n</li>
                <li>Confusi&oacute;n</li>
                <li>Sistema inmunol&oacute;gico debilitado</li>
            </ul>
            <p>Puede conducir a un s&iacute;ndrome similar a la pelagra, con dermatitis seborreica, inflamaci&oacute;n
                de la lengua o glositis, e inflamaci&oacute;n y agrietamiento de los labios, conocido como quilosis.</p>
            <p></p>
            <p>En los ni&ntilde;os, las convulsiones pueden persistir incluso despu&eacute;s del tratamiento con
                anticonvulsivos. Otras deficiencias, como la neuropat&iacute;a perif&eacute;rica, pueden ser
                permanentes.</p>
            <p></p>
            <h2 name="suplementos">Suplementos B6</h2>
            <p>Seg&uacute;n los Institutos Nacionales de Salud (NIH), entre el 28 y el 35 por ciento de la
                poblaci&oacute;n de los Estados Unidos toma suplementos vitam&iacute;nicos que contienen vitamina
                B6.</p>
            <p></p>
            <p>Los suplementos est&aacute;n disponibles en forma de c&aacute;psulas o tabletas.</p>
            <p></p>
            <p>El NIH tambi&eacute;n se&ntilde;ala que la mayor&iacute;a de las personas de todas las edades en los
                EE.UU. consumen suficiente B6. Las personas que tienen m&aacute;s probabilidades de tener niveles bajos
                de B6 son las que beben cantidades excesivas de alcohol, las personas obesas y las mujeres embarazadas y
                lactantes.</p>
            <p></p>
            <p>No hay evidencia de ning&uacute;n efecto adverso por consumir demasiada vitamina B6 en los alimentos.</p>
            <p></p>
            <p>Sin embargo, se ha demostrado que la ingesta de entre 1 y 6 gramos de piridoxina oral al d&iacute;a
                durante 12 a 40 meses causa una neuropat&iacute;a sensorial grave y progresiva y una p&eacute;rdida de
                control del movimiento corporal.</p>
            <p></p>
            <p>Las Gu&iacute;as Alimentarias para los estadounidenses de 2015 a 2020 recomiendan que la mayor&iacute;a
                de los nutrientes provengan de los alimentos. Fomentan el consumo de una dieta equilibrada con alimentos
                densos en nutrientes y mucha fibra diet&eacute;tica.</p>
            <p></p>
            <p></p>
        </Layout>
    )
}

export default Mac
